.left {
  display: flex;
  flex-direction: row;
  padding: 40px;
  margin: 80px 0px;
  text-align: left; }

.right {
  display: flex;
  flex-direction: row;
  padding: 40px;
  text-align: right; }

.project-img {
  width: 50%;
  padding: 40px; }
  .project-img img {
    width: 100%; }

.project-text {
  width: 50%;
  padding: 40px; }
  .project-text h2 {
    color: #363F54;
    font-size: 32px;
    font-weight: bold;
    padding: 20px 0px;
    line-height: 42px; }
  .project-text p {
    margin-left: 20px; }
  .project-text a {
    background: #8A99C0;
    color: white;
    padding: 10px 20px;
    font-size: 12px;
    border-radius: 20px;
    margin-left: 20px; }
