.desktop-wrapper {
  position: relative;
  width: 70%;
  height: 21vw;
  min-width: 200px;
  min-height: 300px;
  animation: fade-in 4s; }
  .desktop-wrapper .desktop-container {
    box-shadow: 0 15px 28px 0 rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
    border-radius: 5px 5px 0px 0px; }
    .desktop-wrapper .desktop-container .desktop-head {
      position: absolute;
      z-index: 1;
      border-radius: 5px 5px 0px 0px;
      top: 0;
      left: 0;
      width: 100%;
      height: 21px;
      background: #fff; }
      .desktop-wrapper .desktop-container .desktop-head .ptn {
        position: absolute;
        height: 5px;
        width: 5px;
        border-radius: 3px;
        top: 50%;
        background: #000;
        opacity: 0.2; }
      .desktop-wrapper .desktop-container .desktop-head .ptn:nth-child(1) {
        left: 6px; }
      .desktop-wrapper .desktop-container .desktop-head .ptn:nth-child(2) {
        left: 16px; }
      .desktop-wrapper .desktop-container .desktop-head .ptn:nth-child(3) {
        left: 26px; }
    .desktop-wrapper .desktop-container .desktop-screen {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;
      padding-top: 21px;
      border-radius: 5px 5px 0px 0px; }
      .desktop-wrapper .desktop-container .desktop-screen img {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        margin-top: 21px;
        animation-name: desktopScroll;
        animation-duration: 5s;
        animation-direction: alternate;
        animation-iteration-count: infinite;
        animation-fill-mode: none;
        animation-timing-function: cubic-bezier(1, 0.005, 0.305, 1); }

@keyframes fade-in {
  10% {
    transform: translateY(30px);
    opacity: 0; }
  100% {
    transform: translateY(0px);
    opacity: 1; } }

@keyframes desktopScroll {
  10% {
    transform: translateY(0%); }
  50% {
    transform: translateY(calc(-50% + 285px)); }
  100% {
    transform: translateY(calc(-100% + 370px)); } }
